import * as Vue from 'vue'
const allVue = Vue
const gh = allVue.h
const isV3 = allVue.version && allVue.version[0] === '3'
import KendoDatePickerMixin from './KendoDatePickerMixin'

export default {
    name: 'kendo-datepicker',
    mixins: [ KendoDatePickerMixin ],
    setup() {
        const v3 = isV3
        return {
            v3
        }
    },
    render (createElement) {
        const h = gh || createElement
        return h('input',{
            props: {
                value: this.value
            }
        })
    }
}


