import * as Vue from 'vue'
const allVue = Vue
const gh = allVue.h
const isV3 = allVue.version && allVue.version[0] === '3'
import KendoMultiViewCalendarMixin from './KendoMultiViewCalendarMixin'

export default {
    name: 'kendo-multiviewcalendar',
    mixins: [ KendoMultiViewCalendarMixin ],
    setup() {
        const v3 = isV3
        return {
            v3
        }
    },
    render (createElement) {
        const h = gh || createElement
        return h('div')
    }
}


